
import {defineComponent} from 'vue';
import {
  IonPage,
  IonCard,
  IonCardContent,
  IonRow,
  IonIcon,
  IonLabel,
  IonCol,
  IonList,
  IonItem,
  IonButtons,
  IonButton,
  alertController
} from '@ionic/vue';
import Header from '@/components/ui/Header.vue';
import Content from '@/components/ui/Content.vue';
import Button from '@/components/ui/Button.vue';
import {mapActions, mapGetters, mapMutations} from 'vuex';
import Loading from '@/components/ui/Loading.vue';
import Address from '@/components/Address.vue';
import {formatPhone} from '../helpers/formatter';
import {removeUser} from "@/api/user";

export default defineComponent({
  name: 'Profile',
  components: {
    Address,
    Loading,
    Button,
    Content,
    Header,
    IonPage,
    IonCard,
    IonCardContent,
    IonRow,
    IonIcon,
    IonLabel,
    IonCol,
    IonList,
    IonItem,
    IonButtons,
    IonButton,
  },
  data() {
    return {
      loading: false,
      isOpenAlert: false
    };
  },
  computed: {
    ...mapGetters(['user', 'token']),
  },
  methods: {
    formatPhone,
    ...mapMutations(['SET_TOKEN', 'SET_USER']),
    ...mapActions(['getUser']),
    chat() {
      const win: Window = window;
      win.location = 'https://wa.me/79502822722';
    },
    logout() {
      this.SET_TOKEN('');
      localStorage.removeItem('jwt');
      localStorage.removeItem('user');
    },
    async remove(id: any) {
      this.loading = true
      await removeUser(id)
      localStorage.removeItem('user')
      localStorage.removeItem('jwt')
      this.SET_TOKEN('')
      this.SET_USER({})
      this.loading = false
      this.$router.replace({name: 'Profile'})
    },
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    async refresh(complete = () => {
    }) {
      try {
        await this.getUser(this.user.id);
      } finally {
        complete();
      }
    },
    async presentAlert(id: any) {
      const alert = await alertController.create({
        header: this.$t('REMOVE'),
        message: this.$t('REMOVE-MESSAGE'),
        buttons: [
          {
            text: this.$t('CANCEL'),
            role: 'cancel',
            handler: () => {
              console.log('cancel')
            },
          },
          {
            text: this.$t('OK'),
            role: 'confirm',
            handler: () => {
              console.log('ok')
              this.remove(id)
            },
          },
        ],
      });

      await alert.present();
      await alert.onDidDismiss();
    }
  },
  async mounted() {
    this.loading = true;
    this.loading = false;
  },
});
